h1 {
    color: purple;
    font-style: italic;
}

li {
    color: grey;
    padding-top: 3px;
    font-size: 15px;
}

.note {
    color: blueviolet;
    padding-top: 5px;
    font-size: 20px;
}

.error {
    color: red;
    background: rgb(255, 251, 1);
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }

.success {
    color: green;
    background-color: lightgray;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    opacity: 1;
    transition: opacity 300ms ease-in;
}

button {
    border: 2px solid blue;
    border-color: blueviolet;
    border-radius: 5px;
    background-color: white;
    color: blue;
    text-align: center;
    font-size: 15px;
    padding: 10px 20px;
    transition-duration: 0.3s;
    margin-top: 10px;
}

button:hover {
    cursor: pointer;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    color: white;
    background-color: blueviolet;
}

input {
    padding: 10px 20px;
    margin-top: 10px;
    height: 15px;
    background-color: antiquewhite;
    border: 2px solid;
    border-color: blueviolet;
}

input:focus-within {
    border-radius: 5px;
}